/* ГЛОБАЛЬНЫЕ */
/* FRONTS */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
/* COLORS */
/* SHADOW */
/* VIEWPORTS */
html {
  height: 100%;
}
body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/* BUTTON */
.button-primary {
  padding: 11px 44px 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  box-sizing: border-box;
}
.button-primary:hover {
  background-color: #5eaa2f;
}
.button-primary:active span {
  opacity: 0.3;
}
.button-second {
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: #444444;
  background-color: #f2f2f2;
  text-decoration: none;
}
/* INPUT */
/* TEXT */
.input-text {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: stretch;
}
.input-text__span {
  margin-right: 13px;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  white-space: nowrap;
  color: #444444;
}
.input-text__input {
  position: relative;
  flex-grow: 1;
  padding: 14px 51px 14px 15px;
  width: calc(100% - 20px);
  height: 43px;
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
  border: none;
  outline: 1px solid #e7e7e7;
  box-sizing: border-box;
}
.input-text__mail {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  right: 15px;
  width: 18px;
  height: 13px;
  fill: none;
  stroke: #222222;
}
.input-text__phone {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  right: 12px;
  width: 20px;
  height: 20px;
}
.input-text__input:focus ~ .input-text__mail {
  stroke: #68b738;
}
.input-text__input:focus ~ .input-text__phone {
  stroke: #68b738;
}
.input-text__input:invalid .input-text__mail {
  fill: #ff8282;
}
.input-text__input:invalid ~ .input-text__phone {
  stroke: #ff8282;
}
.input-text__input:hover {
  outline: 1px solid #cdcdcd;
}
.input-text__input:focus {
  outline: 1px solid #444444;
}
.input-text__input:invalid {
  outline: 1px solid #ff8282;
}
/* AREA */
.input-area {
  display: flex;
  min-height: 150px;
}
.input-area__input {
  width: 100%;
  min-height: 150px;
  padding: 10px 10px 16px 15px;
  font-size: 16px;
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 18px;
  text-transform: uppercase;
  color: #444444;
  border: 1px solid #e7e7e7;
  box-sizing: border-box;
}
.input-area__input:hover {
  outline: 1px solid #cdcdcd;
}
.input-area__input:focus {
  outline: 1px solid #444444;
}
.input-area__input:invalid {
  outline: 1px solid #ff8282;
}
/* CHECKBOX */
.input-checkbox {
  display: flex;
  align-items: center;
}
.input-checkbox__input {
  width: 30px;
  height: 30px;
  margin-right: 13px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  opacity: 0;
}
.input-checkbox__mark {
  position: absolute;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
}
.input-checkbox__input:checked ~ .input-checkbox__mark::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 26px;
  height: 26px;
  background-image: url("../img/icons/tick.svg");
}
.input-checkbox__input:disabled,
.input-checkbox__input:disabled ~ .input-checkbox__span {
  opacity: 0.3;
}
.input-checkbox__input:focus ~ .input-checkbox__span {
  outline: 1px solid #444444;
}
.input-checkbox__span {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
/* RADIO */
.input-radio {
  display: flex;
  align-items: center;
}
.input-radio__input {
  width: 30px;
  height: 30px;
  margin: 0 15px 0 0;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
  opacity: 0;
}
.input-radio__mark {
  position: absolute;
  width: 30px;
  height: 30px;
  margin-right: 15px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  box-sizing: border-box;
}
.input-radio__input:checked ~ .input-radio__mark::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 13px;
  content: "";
  background-color: #68b738;
  border-radius: 50%;
}
.input-radio__span {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;
}
.input-radio__input:disabled,
.input-radio__input:disabled ~ .input-radio__span {
  opacity: 0.3;
}
/* RANGE */
.input-range {
  position: relative;
  width: 86px;
  height: 16px;
  border-radius: 6px;
  cursor: pointer;
  background: #ffffff;
  border: 1px solid #cdcdcd;
  box-sizing: border-box;
}
.input-range__thumb {
  position: absolute;
  left: 50%;
  transform: translateX(-100%);
  height: 6px;
  width: 36px;
  margin-top: 4px;
  box-sizing: border-box;
  border-radius: 6px;
  background-color: #68b738;
  cursor: pointer;
  border: none;
}
