@media (min-width: 768px) {
  /* BUTTON */
  .button-primary {
    padding: 10px 26px;
    font-size: 16px;
    line-height: 20px;
  }
  /* TEXT */
  .input-text__span {
    margin-right: 18px;
    font-size: 20px;
    line-height: 30px;
  }
  .input-text__input {
    height: 52px;
    padding-top: 11px;
    padding-left: 18px;
    font-size: 20px;
    line-height: 30px;
  }
  /* AREA */
  .input-area__input {
    padding-left: 17px;
    min-height: 175px;
    font-size: 20px;
    line-height: 30px;
  }
  /* CHECKBOX */
  .input-checkbox__span {
    font-size: 20px;
    line-height: 30px;
  }
  .input-checkbox__input {
    width: 40px;
    height: 40px;
  }
  .input-checkbox__mark {
    width: 40px;
    height: 40px;
  }
  /* RADIO */
  .input-radio__span {
    font-size: 20px;
    line-height: 30px;
  }
  .input-radio__input {
    width: 40px;
    height: 40px;
    margin-right: 20px;
  }
  .input-radio__mark {
    width: 40px;
    height: 40px;
  }
  .input-radio__input:checked ~ .input-radio__mark::before {
    width: 18px;
    height: 18px;
  }
  /* RANGE */
  .input-range {
    margin: 17px 0;
    width: 100%;
    height: 6px;
    background: #dcdcdc;
  }
  .input-range__thumb {
    transform: translateX(-50%);
    height: 34px;
    width: 34px;
    margin-top: -15px;
    padding: 4px;
    border-radius: 17px;
    background-color: #68b738;
    border: 13px solid #ffffff;
    outline: 1px solid #cdcdcd;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));
  }
  .input-range__thumb:hover {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }
  .input-range__thumb:active {
    outline-color: #68b738;
    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  }
}
@media (min-width: 1200px) {
  .input-area__input {
    min-height: 175px;
    padding-left: 18px;
  }
  .input-text__mail {
    right: 20px;
  }
  .input-text__phone {
    right: 15px;
  }
}
