  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
.catalog__order {
  grid-column: 1/3;
  width: 100%;
  font-size: 16px;
}
.catalog__more {
  margin: 0 0 4px 0;
  text-transform: uppercase;
}
.catalog__bigger {
  margin: 0 0 21px 0;
  font-family: "Arial", "Tahoma", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}
